.container {
    // height: 100%;
    // width: 100%;
    display: flex;

    .side-bar {
        width: 25%;
        height: 100vh;
        background-color: #ece5dd;

        .side-bar-header {
            display: flex;
            justify-content: space-between;
            background-color: #128c7e;
            color: #fff;
        }
    }

    .messages {
        height: 100vh;
        width: 75%;
        background-color: #c5c5c5;

        .chat-messages {
            height: calc(100vh - 38px) !important;

            .message {
                padding: 10px;
                margin-bottom: 15px;
                background-color: #e6e9ff;
                border-radius: 5px;
            }

            .meta {
                font-size: 15px;
                font-weight: bold;
                color: var(#7386ff);
                opacity: 0.7;
                margin-bottom: 7px;
            }

            span {
                color: #777;
            }
        }

        .footer {
            width: 100%;
            .form-elements {
                display: flex;
            }
            input {
                width: 500px;
            }
        }
        .field {
            width: 100%;
        }
    }
}
