.landing {
    position: relative;
    background: url("../../assets/background.jpg") no-repeat center center/cover;
    height: 100vh;

    .dark-overlay {
        background-color: rgba(0, 0, 0, 0.7);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .landing-inner {
        color: #fff;
        height: 100%;
        width: 80%;
        margin: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
    .x-large {
        font-size: 3rem;
    }
    .lead {
        font-size: 1rem;
    }

    .button-primary {
        background-color: #337ab7;
        color: #fff;
        border-color: #2e6da4;
    }

    .button {
        margin: 2px;
        width: 100px;
        height: 25px;
        border: 1px solid transparent;
        border-radius: 4px;
        font-weight: 400;
        text-align: center;
        vertical-align: middle;
    }
}
