.login-form {
    background-color: #cccccbfb;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 2px;
    box-shadow: 5px 10px 8px #c5c5c5;
    height: 50%;
    width: 50%;

    p {
        text-align: center;
    }
    .form-input {
        margin-left: 20px !important;
    }
}
